import Vue from 'vue'
import Vuex from 'vuex'
import menus from './modules/menus'
import actions from './modules/actions'
import content from './modules/content'
import cities from './modules/cities'
import popups from './modules/popups'
import auth from './modules/auth'
import users from './modules/users'
import mail from './modules/mail'
import forms from './modules/forms'
import opinions from './modules/opinions'
import partner from './modules/partner/partner'
import teams from './modules/partner/teams'
import partnerActions from './modules/partner/actions'
import partnerCoupons from './modules/partner/coupons'
import masks from './modules/masks'
import options from './modules/options'

Vue.use(Vuex)

export default new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production',
    modules: {
        menus,
        actions,
        content,
        cities,
        popups,
        auth,
        users,
        mail,
        forms,
        opinions,
        partner,
        teams,
        partnerActions,
        partnerCoupons,
        masks,
        options
    },
    state: {
        onTabClick: false,
        notifyPageVersion: '',
    },
    mutations: {
        setTabClick(state, data){
            state.setTabClick = true;
        },
        setNotifyPageVersion(state, data){
            state.notifyPageVersion = data;
        },
    },
    actions: {

    }
})
