<template>
    <div class="find-coupon">
        <div class="ttl">Поиск<br>и погашение<br>Купонов</div>
        <div class="find-inpt" id="find-inpt">
            <input v-model="searchQuery"
                   :class="{error:phoneSearch.error}"
                   @keyup.enter = "getSearch()"
                   ref="searchInput"
                   v-mask="masks.phone"
                   :is-block-arrow-phone="true"
                   type="tel"
                   placeholder="Телефон покупателя"
                   value="">
            <svg class="icon-h-search">
                <use xlink:href="@/assets/img/mob-sprite.svg#h-search"></use>
            </svg>
            <svg v-if="searchQuery.length > 0" @click="searchQuery = ''" class="icon-c-remove">
                <use xlink:href="@/assets/img/mob-sprite.svg#c-remove"></use>
            </svg>
            <button class="btn"  @click="getSearch"><span>Искать</span></button>
        </div>
    </div>
</template>

<script>

    import { mapState } from 'vuex'
    import {isPhone} from '@/helpers/validate.js';
    import {scrollTo} from '@/helpers/animate.js';

    export default {
        name: "SearchRepayment",
        data(){
            return {
                isFocus: false,
            }
        },
        computed: {
            searchQuery: {
                get () {
                    return this.$store.state.partnerCoupons.phoneSearch.value
                },
                set (value) {
                    if(this.phoneSearchFocus > 0){
                        this.$store.commit('partnerCoupons/updatePhoneSearchFocus', 0);
                    }
                    this.$store.commit('partnerCoupons/updatePhoneSearch', value)
                }
            },
            ...mapState({
                masks: state =>  state.masks.items,
                phoneSearch: state =>  state.partnerCoupons.phoneSearch,
                phoneSearchFocus: state =>  state.partnerCoupons.phoneSearch.focus
            }),
        },
        watch: {
            phoneSearchFocus(newVal, oldVal) {
                if(newVal === 1) {
                    this.$refs.searchInput.focus();
                } else if(newVal >= 2) {
                    this.$refs.searchInput.focus();
                    this.$store.commit('partnerCoupons/updatePhoneSearchFocus', 0);
                    this.$store.dispatch('partnerCoupons/setPhoneSearchError');
                }
            }
        },
        methods: {
            focus(){
                if(!this.$store.state.partnerCoupons.phoneSearch.focus) {
                    this.$store.dispatch('partnerCoupons/updatePhoneSearchFocus', true);
                    this.$refs.searchInput.focus();
                } else {
                    this.$store.dispatch('partnerCoupons/setPhoneSearchError');
                }
            },
            getSearch () {
                if(isPhone(this.phoneSearch.value)) {
                    this.$store.dispatch('partnerCoupons/getRepaymentList');
                } else {
                    this.$store.dispatch('partnerCoupons/setPhoneSearchError');
                }
                setTimeout(function(){
                    scrollTo('#find-inpt',true,-30);
                },300);
            },
        }
    }
</script>

<style>
    .h-search{
        position: relative;
    }
    .submit{
        position: absolute;
        top: 7px;
        right: 8px;
        background: none;
        border: none;
        padding: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
        z-index: 1;
        cursor: pointer;
    }
</style>
