import axios from 'axios'
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'
import qs from "qs";

const state = {
    commission: {
        items: [],
    },
    bills: {
        items: [],
    },
    phoneSearch: {
        value: '',
        error: false,
        focus: 0,
    },
    repaymentCoupons: {
        items: [],
        user: false,
        other: 0,
    },
    isSend: false,
    currentItem: {},
    isLoad: false,
}

// getters
const getters = {
    getTotalCommission: state => {
        let sum = 0;
        for(let item of state.commission.items) {
            sum += item.our_no_format
        }
        return sum;
    },
}

const actions = {
    getRepaymentList(context) {
        return new Promise((resolve, reject) => {
            context.commit('setRepaymentEmpty');
            context.commit('setRepaymentSend',false);
            if (!state.isLoad) {
                context.commit('setLoad', true);
                axios.get(process.env.VUE_APP_API_PATH+'/partner/repayments/', {
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                    params: {
                        phone: context.state.phoneSearch.value
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    context.commit('setLoad', false);
                    context.commit('setRepaymentSend',true);
                    resolve();
                    if (typeof response.data === 'object' && +response.data.error === 0 && response.data.result !== undefined) {
                        context.commit('setRepayment', response.data.result);
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    context.commit('setLoad', false);
                    context.commit('setRepaymentSend',true);
                    reject();
                });
            }
        });
    },
    getCommissionItems(context) {
        context.commit('setCommissionItems', {items:[]});
        if(!state.isLoad){
            context.commit('setLoad', true);
            axios.get(process.env.VUE_APP_API_PATH+'/partner/commissions/',{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
            }).then(response => {
                context.commit('setLoad', false);
                if(typeof response.data === 'object' && +response.data.error === 0 && response.data.result !== undefined) {
                    context.commit('setCommissionItems', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
    getBillItems(context) {
        context.commit('setBillItems', {items:[]});
        if(!state.isLoad){
            context.commit('setLoad', true);
            axios.get(process.env.VUE_APP_API_PATH+'/partner/bills/',{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
            }).then(response => {
                context.commit('setLoad', false);
                if(typeof response.data === 'object' && +response.data.error === 0 && response.data.result !== undefined) {
                    context.commit('setBillItems', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
    setPhoneSearchError(context) {
        context.commit('updatePhoneSearchError', true);
        setTimeout(()=>{
            context.commit('updatePhoneSearchError', false);
        },600)
    },
    sendRepaymentAnnul(context, data) {
        return new Promise((resolve, reject) => {
            if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined) {
                if (!state.sendForm) {
                    axios.put(process.env.VUE_APP_API_PATH + '/partner/repayments/' + data.id, {}, {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: {
                            update: 'annul',
                            comment: data.text,
                        },
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },

                    }).then(response => {
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            context.dispatch('getRepaymentList').then(() => {
                                resolve();
                            });
                        } else {
                            if (+response.data.error === 401) {
                                context.dispatch('auth/logout', {}, {root: true})
                            }
                        }
                    }).catch(function (error) {
                    });
                } else {
                    resolve();
                }
            } else {
                context.dispatch('auth/logout',{},{root:true})
                resolve();
            }
        });
    },
    sendRepaymentRepay(context, data) {
        return new Promise((resolve, reject) => {
            if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined) {
                if(!state.sendForm) {
                    axios.put(process.env.VUE_APP_API_PATH+'/partner/repayments/'+data.id,{},{
                        headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                        withCredentials: true,
                        params: {
                            update: 'repay',
                            price: data.price,
                            percent: data.percent,
                            discount: data.discount,
                            comment: data.text,
                        },
                        paramsSerializer: function(params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },

                    }).then(response => {
                        if(typeof response.data === 'object' && +response.data.error === 0) {
                            context.dispatch('getRepaymentList').then(()=>{
                                resolve();
                            });
                        } else {
                            if( +response.data.error===401){
                                context.dispatch('auth/logout',{},{root:true})
                            }
                        }
                    }).catch(function (error) {
                    });
                } else {
                    resolve();
                }
            } else {
                context.dispatch('auth/logout',{},{root:true})
                resolve();
            }
        });
    },
}

const mutations = {
    setCommissionItems(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            Vue.set(state.commission,'items', data.items);
        }
    },
    setRepayment(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            Vue.set(state,'repaymentCoupons', data);
        }
    },
    setRepaymentEmpty(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            Vue.set(state,'repaymentCoupons', {
                items: [],
                user: false,
                other: 0
            });
        }
    },
    setBillItems(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            Vue.set(state.bills,'items', data.items);
        }
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setRepaymentSend(state, data) {
        state.isSend = data;
    },
    updatePhoneSearch(state, data) {
        state.phoneSearch.value = data;
    },
    updatePhoneSearchError(state, data) {
        state.phoneSearch.error = data;
    },
    updatePhoneSearchFocus(state, data) {
        state.phoneSearch.focus = data;
    },
    setCurrent(state, data) {
        Vue.set(state,'currentItem', data);
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}