<template>
    <div class="find-redeem" id="app">

        <header class="fr-header" v-if="partner.success">
            <div class="user">
                <svg class="icon-user">
                    <use xlink:href="@/assets/img/mob-sprite.svg#user"></use>
                </svg>
                <div class="right"><span class="name">{{user.lastName}} {{ user.firstName ? user.firstName[0] : '' }}</span><span class="company" v-html="partner.name"></span></div>
            </div>
            <div v-if="user.lastName" class="exit" @click="logout()">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#exit"></use>
                </svg>
            </div>
        </header>



        <main v-show="isLoadedPartner" id="main">
            <router-view v-if="partner.success"/>
            <section v-else>
                <div class="popup login-signup">
                    <div class="popup-header">
                        <div class="popup-name"  v-if="!token && !isBanned && !isBannedPartner">Вход в кабинет партнёра</div>
                          <div class="popup-name"  v-else >Личный кабинет партнёра</div>
                    </div>
                    <section class="popup-content partner-errors">
                        <div class="inner">
                            <auth :type-lk="'partner'" v-if="!token && !isBanned && !isBannedPartner"></auth>
                            <div class="wrapper-content" v-else-if="isBanned && !isBannedPartner">
                                <div class="sect-blank">
                                    <div class="wrap text-center">
                                        <div class="text-size-xl text-extrabold text-color-red mt16 partner-error-ttl">Слишком много<br>попыток входа!</div>
                                        <div class="mt">Вы заблокированы на 12 часов.</div>
                                        <div class="mt">Если у вас что-то срочное,<br><a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                    </div>

                                    <div class="mini-zems"></div>
                                </div>
                                <!-- end sect-blank -->
                            </div>
                            <div class="wrapper-content" v-else>
                                <div class="sect-blank">
                                    <div class="wrap text-center" v-if="partner.isBan">
                                        <div class="text-size-xl text-extrabold text-color-red mt16 partner-error-ttl">Личный кабинет<br>#{{ partner.login }}<br>заблокирован!</div>
                                        <div class="mt">Причина блокировки:<br>{{ partner.banReason }}</div>
                                        <div class="mt">Если это произошло по ошибке,<br><a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                    </div>
                                    <div class="wrap text-center" v-else-if="partner.isRefuse">
                                        <div class="text-size-xl text-extrabold text-color-red mt16 partner-error-ttl">Действие кабинета<br>#{{ partner.login }}<br>приостановлено!</div>
                                        <div class="mt">Если это произошло по ошибке,<br><a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                    </div>
                                    <div class="wrap text-center" v-else>
                                        <div class="text-size-xl text-extrabold text-color-red mt16 partner-error-ttl">
                                            У номера<br>{{(user.phone)?user.phone:partner.login}}<br>
                                            нет доступа<br>в этот раздел
                                        </div>
                                    </div>
                                </div>
                                <!-- end sect-blank -->
                            </div>
                        </div>

                    </section>
                </div>
            </section>
        </main>
        <popup class="mailzems"  :title="'Письмо руководителю ZEMSKIDKI'" :is-set-styled="true" :show="popups.mailZems.show" :name="popups.mailZems.name">
            <mail-zems></mail-zems>
        </popup>
    </div>
</template>




<script>
    import { mapState } from 'vuex'
    import TopMenu from '@/components/TopMenu.vue'
    import SearchTop from '@/components/SearchTop.vue'
    import Popup from '@/components/modal/Popup.vue'
    import CitySelect from '@/components/CitySelect.vue'
    import MailZems from '@/components/modal/MailZems.vue'
    import Auth from '@/components/user/auth/AuthPersonal.vue'
    import ManyAuth from '@/components/modal/ManyAuth.vue'
    import Tooltip from '@/components/Tooltip.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import {scrollTo} from '@/helpers/animate.js';
    import SearchRepayment from '@/components/partner/SearchRepayment.vue'

    export default {
        components: {
            TopMenu,
            SearchTop,
            Popup,
            CitySelect,
            Auth,
            MailZems,
            ManyAuth,
            Tooltip,
            SubmitBtn,
            SearchRepayment
        },
        created() {
            this.$store.dispatch('users/getCurrent');
            this.$store.dispatch('options/getList').then(()=>{
                this.$store.dispatch('partner/getCurrent').then(()=>{
                    if(this.partner.redirect && this.$route.name !== 'partner') {
                        this.$router.push(this.partner.redirect);
                    }
                });
                this.$store.dispatch('cities/setCityCookies');
                this.$store.dispatch('actions/setFavoritesCookies');
                this.$store.dispatch('actions/setVisitedCookies');
                this.$store.dispatch('users/getCookiesWarning');
                this.$store.dispatch('cities/getCities');
            });;
        },
        mounted(){
            /*this.$store.subscribe((mutation, state) => {
                if(mutation.type=='popups/open'){
                    if(state.popups.items.notifyPageReset.show==true){
                        this.showNotifyPageReset();
                    }
                }
            });*/
        },
        data(){
            return {
                buttons: {
                    copy: {
                        state: 'normal'
                    },
                    stop: {
                        state: 'normal'
                    },
                    active: {
                        state: 'normal'
                    },
                    delete: {
                        state: 'normal'
                    },
                    saveAction: {
                        state: 'normal'
                    },
                    savePartner: {
                        state: 'normal'
                    },
                    moderateAction: {
                        state: 'normal'
                    },
                }
            }
        },
        computed: {
            userCheck() {
                if(!!this.user.firstName && !!this.user.secondName && !!this.user.lastName && !!this.user.email){
                    return true;
                }
                return false;
            },
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                popups: state => state.popups.items,
                partner: state => state.partner.current,
                action: state => state.partnerActions.detail,
                statuses: state => state.partnerActions.statuses,
                user: state => state.users.user,
                isLoadedPartner: state => state.partner.isLoaded,
                isBanned: state => state.auth.isBanned,
                isBannedPartner: state => state.auth.isBannedPartner,
                currentCity:  state => state.cities.current,
                currentCityIp:  state => state.cities.currentCityIp,
                currentStep: state => state.partnerActions.currentStep,
                tmpId: state => state.partnerActions.tmpId,
            }),
        },
        methods: {
            showPopup(popup) {
                this.$store.commit('popups/open',{name:popup});
            },
            openAuthorization: function () {
                this.$store.commit('auth/setIsRedirect', true);
                this.$store.commit('popups/open', {name: this.popups.publicAuth.name});
            },
            logout() {
                this.$store.commit('partnerCoupons/setRepaymentEmpty');
                this.$store.dispatch('auth/logout');
            },
            openCitySelect() {
                this.$store.commit('popups/open', {name: this.popups.citySelect.name});
            },
            openMailZems: function () {
                this.$store.commit('popups/open', {name: this.popups.mailZems.name});
            },

            showNotifyPageReset(){
                this.$refs.tooltipNotifyReset.tooltipShow();
            }
        }

    }
</script>



<style lang="sass">
    body
</style>
