import axios from 'axios'
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'
import qs from "qs";

const state = {
    items: [],
    isLoad: false,
}

// getters
const getters = {
    getByXmlId: state => id => {
        return [...state.items].find(city => city.xmlId === id)
    },
}

const actions = {
    getItems(context) {
        axios.get(process.env.VUE_APP_API_PATH+'/partner/teams/',{
            headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            withCredentials: true,
        }).then(response => {
            if(typeof response.data === 'object' && +response.data.error === 0) {
                context.commit('setItems', response.data.result);
            } else {
                if( +response.data.error===401){
                    context.dispatch('auth/logout',{},{root:true})
                }
            }
        }).catch(function (error) {
        });
    },
    add(context,data) {
        return new Promise((resolve, reject) => {
            axios.post(process.env.VUE_APP_API_PATH+'/partner/teams/',{},{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    permission: data.permission,
                    secondName: data.secondName,
                    name: data.name,
                    lastName: data.lastName,
                    phone: data.phone,
                    email: data.email,
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                resolve()
                if(typeof response.data === 'object' && +response.data.error === 0 && response.data.result.item.id) {
                    context.commit('addItem', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                resolve()
            });
        });
    },
    save(context,data) {
        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API_PATH+'/partner/teams/'+data.id,{},{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    permission: data.permission,
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                resolve();
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('updateItemPerm', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    ban(context,data) {
        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API_PATH+'/partner/teams/'+data.id,{},{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    status: 'ban',
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                resolve();
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('updateItemBan', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {

            });
        });
    },
    unban(context,data) {
        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API_PATH+'/partner/teams/'+data.id,{},{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    status: 'unban',
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                resolve()
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('updateItemBan', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
}

const mutations = {
    setItems(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            Vue.set(state,'items', data.items);
        }
    },
    addItem(state, data) {
        if(typeof data === 'object' && data.item !== undefined) {
            let items = {...state.items};
            items.users.push(data.item);
            Vue.set(state,'items', items);
        }
    },
    updateItemPerm(state, data) {
        if(typeof data === 'object' && data.item !== undefined) {
            for(let i in state.items.users){
                if(+state.items.users[i].id === +data.item.id){
                    Vue.set(state.items.users[i],'permission', data.item.permission);

                }
            }
        }
    },
    updateItemBan(state, data) {
        if(typeof data === 'object' && data.item !== undefined) {
            for(let i in state.items.users){
                if(+state.items.users[i].id === +data.item.id){
                    Vue.set(state.items.users[i],'isBanned', data.item.isBanned);
                }
            }
        }
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}